import React, { useState, useEffect, useContext } from 'react'
import styled from 'styled-components'
import Header from '../components/Header'
import Footer from '../components/Footer'
import MenuCom from '../components/MenuCom'
import Pricing from '../components/Pricing'
import ShopCard from '../components/ShopCard'
import CategoryCom from '../components/CategoryCom'
import Aos from 'aos'
import "aos/dist/aos.css"
import Img from '../images/bg_4.jpg'
import {Context} from '../context'
const url = 'https://www.thecocktaildb.com/api/json/v1/1/search.php?s='

const Menu = () => {
    const {addtoCart} = useContext(Context)
    useEffect(() => {
        Aos.init({
            duration: 3000
        })
    }, [])
    // const [loading, setLoading] = useState(true)
    const [array, setArray] = useState([])
    const [data, setData] = useState([])
    const [page, setPage] = useState(1)
    const [createButton, setCreateButton] = useState([])
    const getCoctail = async () => {
        const response = await fetch(url)
        const data = await response.json()
        const { drinks } = data
        setArray(drinks)
        setData(drinks)
        setArray(DisplayList(drinks, 4, page))
    }

    useEffect(() => {
        getCoctail()
    }, [page]);

    const nextPage = (index) => {
        setPage(index + 1)
        setArray(DisplayList(array, 4, page))
        console.log(page)
    }

    // pagination
    function DisplayList(array, rows, current_page) {
        current_page--
        let start = current_page * rows;
        let end = start + rows;
        let pagination = array.slice(start, end);
        setCreateButton(pagination)
        return pagination 
    }
    // SearchBar
    const handlerFilterName = (e) => {
        const strGlass = e.target.value.toLowerCase();
        const newArray = data.filter(el => {
            return el.strGlass.toLowerCase().includes(strGlass);
        })
        setArray(newArray)
        
        
    }
      // Ñ„ÑƒÐ½ÐºÑ†Ð¸ÑÐ½Ð¸ ÐºÐ¾Ñ€Ð·Ð¸Ð³Ð°Ð½Ð° ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚ ÐºÐ¾ÑˆÐ¸Ñˆ ÑƒÑ‡ÑƒÐ½ 
    

    return (
        <Section>
            <Header name="Our Menu" />
            <MenuCom />
            <Card className="container">

                <div className="text">
                    <h2 className="heading px-5">DRINKS</h2>
                    <p className="text mb-5">Far far away, behind the word mountains, far from
                        the countries Vokalia and <br />Consonantia, there live the blind texts.</p>
                </div>
                <form className="d-flex">
                    <input onChange={(e) => handlerFilterName(e)} className="form-control bg-transparent me-2 text-white" type="search" placeholder="Search" aria-label="Search" />
                </form>
                <div className="align-items-center row d-flex">
                    {array.map(el => {

                        return (

                            <div key={el.idDrink} data-aos="flip-left"
                                data-aos-easing="ease-out-cubic"
                                data-aos-duration="2000" id="Card" className="col-lg-6 col-sm-12">
                                <div className="card text-center">
                                    <div className="card-img m-auto">
                                        <img src={el.strDrinkThumb} alt="Photo" className="img " />
                                    </div>
                                    <div className="card-heder">
                                        <h3 className="card-name">{el.strGlass}</h3>
                                        <p className="card-text">Put a copper coin in a coffe-cup and fill up with coffee until you no longer see the coin, then add alcohol until you see the coin. Norwegian speciality.</p>
                                        <p className="price">$ 15.67</p>
                                    </div>
                                    <button key={el.idDrink} onClick={() =>  addtoCart(el)} className="btn btn-outline-warning w-50 m-auto">Add to card</button>
                                </div>
                            </div>
                        )

                        
                    })}
                    <div className="btn-container">
                        {createButton.map((el, index) => {
                            return <button key={el.idDrink  } className='pagination-btn' onClick={() => nextPage(index)}>{index + 1}</button>
                        })}
                    </div>

                </div>
            </Card>
            <Pricing />
            <CategoryCom />
            <Footer />
        </Section>
    )
}
const Section = styled.div`
    background: url(${Img}) fixed;
    .text{
        padding-bottom: 1rem !important;
    }
`
const Card = styled.div`
    .heading{
        text-align: center;
        font-size: 30px;
        font-weight: 400;
        color: black;
        line-height: 1.4;
        font-family: "Josefin Sans", Arial, sans-serif;
        color: #fff;
    }
    .btn-container{
        text-align: center;
        .pagination-btn{
           
            border-radius: 5px;
            border: 1px solid #fac564;
            color: #fac564;
            background: none;
            width: max-content;
            outline: none;
            /* border: none; */
            padding: 0.5rem;
            margin: 0.5rem; 
            margin-top: 3rem;
            &:hover{
                color: #fff;
                background: #fac564;
        }
    }
    }
    
p{
    display: block; 
    text-align: center;
    font-family: "Poppins", Arial, sans-serif;
    font-size: 15px;
    line-height: 1.8;
    font-weight: 300;
    margin: 0 0 16px;
    color: gray;
    margin-block-start: 1em;
    margin-block-end: 1em;
}
    #Card{
        padding: 2em 0 0 2em;
        @media (max-width: 500px){
            padding: 1em 2em;
        }
    }
    .card-heder h3{
        margin-bottom: 0.5rem;
    font-family: inherit;
    font-weight: 500;
    line-height: 1.2;
    font-size: 1.7rem;
    font-family: "Josefin Sans", Arial, sans-serif;
    color: #fac564;
    }
    .card-heder{
        padding: 1em;
        .card-text{
            font-family: "Poppins", Arial, sans-serif;
            font-size: 15px;
            line-height: 1.8;
            font-weight: 300;
            color: gray;
        }
        .price{
            color: #fac564;
        }
    }
    .card{
        margin: auto;
        background: none;
        width: 80%;
        height: 100%
    }
    .img{
        width: 200px;
        height: 200px;
        border-radius: 50%;
        object-fit: cover;
    } 
`
export default Menu
