import React from 'react'
import styled from 'styled-components'
import Img1 from '../images/person_1.jpg'
import Img2 from '../images/person_2.jpg'
import Img3 from '../images/person_3.jpg'
import Img4 from '../images/person_4.jpg'
import Aos from 'aos'
import "aos/dist/aos.css"
import { useEffect } from 'react'
const CheffCard = () => {
    useEffect(() => {
        Aos.init({
            duration: 2000
        })
    }, [])
    return (
        <Section>
            <div className="container">
                <div  data-aos="fade-right" className="row justify-content-center mb-5 pb-3">
                    <div className="col-md-7 heading-section text-center">
                        <h2 className="mb-4">Our Chef</h2>
                        <p className="mt-5">Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts.</p>
                    </div>
                </div>
                <div className="row  g-2">
                    <div  data-aos="flip-left" className="col-lg-3 col-md-3 col-sm-12 d-flex mb-sm-4">
                        <div className="staff">
                            <div className="img1 mb-4">

                            </div>
                            <div className="info text-center">
                                <h3>Tom Smith</h3>
                                <span className="position">Hair Specialist</span>
                                <div className="text">
                                    <p>Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts.</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div data-aos="flip-left" className="col-lg-3 col-md-3 col-sm-12 d-flex mb-sm-4">
                        <div className="staff">
                            <div className="img2 mb-4">

                            </div>
                            <div className="info text-center">
                                <h3>Mark Wilson</h3>
                                <span className="position">Beard Specialist</span>
                                <div className="text">
                                    <p>Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts.</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div data-aos="flip-left" className="col-lg-3 col-md-3 col-sm-12 d-flex mb-sm-4">
                        <div className="staff">
                            <div className="img3 mb-4">

                            </div>
                            <div className="info text-center">
                                <h3>Patrick Jacobson</h3>
                                <span className="position">Hair Stylist</span>
                                <div className="text">
                                    <p>Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts.</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div data-aos="flip-left" className="col-lg-3 col-md-3 col-sm-12 d-flex mb-sm-4">
                        <div className="staff">
                            <div className="img4 mb-4">

                            </div>
                            <div className="info text-center">
                                <h3>Ivan Dorchsner</h3>
                                <span className="position">Beard Specialist</span>
                                <div className="text">
                                    <p>Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts.</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Section>
    )
}
const Section = styled.div`
    padding: 5em 0;
    .heading-section{
     h2{
        font-size: 30px;
        font-weight: 400;
        color: #fff;
        text-transform: uppercase;
    }
    p{
    font-family: "Poppins", Arial, sans-serif;
    font-size: 15px;
    line-height: 1.8;
    font-weight: 300;
    color: gray;
    }
    }
     .staff  .img1, .img2, .img3, .img4{  
        width: 140px;
    height: 140px;
    border: 1px solid #8d5d05;
    margin: 0 auto;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    border-radius: 50%;
    -moz-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    -webkit-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
.img1{
        background: url(${Img1});
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
    
    }
    .img2{
        background: url(${Img2});
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
    }
.img3{
        background: url(${Img3});
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
       
}
.img4{
        background: url(${Img4});
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
    
}
.staff {
    padding: 25px;
    bordeR: 1px solid #5c3d03;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    border-radius: 4px;
    :hover{
        bordeR: 1px solid #5c3d03;
    }
    h3{
        line-height: 1.4;
        color: #fac564;
        font-weight: 400;
        font-size: 1.17em;
        font-family: "Josefin Sans", Arial, sans-serif;
    }
   .info span.position {
    display: block;
    margin-bottom: 15px;
    font-size: 13px;
    font-weight: 600;
    color: #cccccc;
   }
   p{
    font-family: "Poppins", Arial, sans-serif;
    font-size: 15px;
    line-height: 1.8;
    font-weight: 300;
    color: gray;
   }
   :hover{
       border: 1px solid yellow;
   }
}
    
`

export default CheffCard
