import React from 'react'
import styled from 'styled-components'
import { Link } from 'react-router-dom'

const ContactOrg = () => {
    return (
        <Section className="contact-section">
            <div className="container mt-5">
                <div className="row block-9">
                    <div className="col-md-4 contact-info">
                        <div className="row">
                            <div className="col-md-12 mb-4">
                                <h2 className="h4">Contact Information</h2>
                            </div>
                            <div className="col-md-12 mb-3">
                                <p><span className="text-white">Address:</span> 198 West 21th Street, Suite 721 New York NY 10016</p>
                            </div>
                            <div className="col-md-12 mb-3">
                                <p><span className="text-white">Phone:</span> <Link to="tel://1234567920">+ 1235 2355 98</Link></p>
                            </div>
                            <div className="col-md-12 mb-3">
                                <p><span className="text-white">Email:</span> <Link to="mailto:info@yoursite.com">info@yoursite.com</Link></p>
                            </div>
                            <div className="col-md-12 mb-3">
                                <p><span className="text-white">Website:</span> <Link to="#">yoursite.com</Link></p>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-1"></div>
                    <div className="col-md-6">
                        <form action="#" className="contact-form">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <input type="text" className="form-control" placeholder="Your Name" />
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <input type="text" className="form-control" placeholder="Your Email" />
                                    </div>
                                </div>
                            </div>
                            <div className="form-group">
                                <input type="text" className="form-control" placeholder="Subject" />
                            </div>
                            <div class="form-group">
                                <textarea name="" id="" cols="30" rows="7" className="form-control" placeholder="Message"></textarea>
                            </div>
                            <div class="form-group">
                                <input type="submit" value="Send Message" className="btn btn-outline-warning py-3 px-5" />
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </Section>
    )
}
const Section = styled.div`
     padding: 5em 0;
     .contact-info{
         h2{
            line-height: 1.4;
            color: #fff;
            font-weight: 400;
            font-family: "Josefin Sans", Arial, sans-serif;
         }
         p{
             color: gray;
             text-align: start;
             font-family: "Poppins", Arial, sans-serif;
             line-height: 1.8;
             a{
                 text-decoration: none;
                 color: #fac564;
             }
         }
     }
     .contact-form .form-group{
        padding: 0 0 2em 0;
     }
     .contact-form .form-control {
    border: transparent !important;
    border-bottom: 1px solid rgba(255, 255, 255, 0.08) !important;
    height: 58px !important;
    padding-left: 0;
    padding-right: 0;
    background: transparent !important;
    color: rgba(255, 255, 255, 0.9) !important;
    font-size: 13px;
    border-radius: 0px;
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
}
.block-9 .form-control {
    outline: none !important;
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
    font-size: 15px;
}
`
export default ContactOrg
