import React,{useEffect} from 'react'
import img2 from "../images/bg_4.jpg"
import img1 from "../images/about.jpg"
import styled from 'styled-components'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPizzaSlice } from "@fortawesome/free-solid-svg-icons";
import Aos from 'aos'
import "aos/dist/aos.css"
const Welcom = () => {
        useEffect(() => {
            Aos.init({
                duration: 3000
            })
        }, [])
    return (
        <Welcome className="d-md-flex align-items-center">
            <div className="image-restauran col-lg-6">
                <img style={{width: "100%", height: "450px", objectFit: "cover"}} src={img1} alt="" />
            </div>
            <div data-aos="fade-up" className="col-lg-6 fix py-5">
                <div className="title px-5">
                    <h2 className="text-center">Welcome to <span class="flaticon-pizza"><FontAwesomeIcon icon={faPizzaSlice} /> Pizza</span> A     Restaurant</h2>
                </div>
                <div className="px-5">
                    <p className="text">On her way she met a copy. The copy warned the Little Blind Text, that where it came from it would have been rewritten a thousand times and everything that was left from its origin would be the word "and" and the Little Blind Text should turn around and return to its own, safe country. But nothing the copy said could convince her and so it didnâ€™t take long until a few insidious Copy Writers ambushed her, made her drunk with Longe and Parole and dragged her into their agency, where they abused her for their.</p>
                </div>
            </div>
        </Welcome>
    )
}
const Welcome = styled.div`
    .title h2 {
    color: white;
    font-size: 30px;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 1.4;
    font-weight: 400;
    font-family: "Josefin Sans", Arial, sans-serif;
    margin-bottom: 1.5rem !important;
    span{
        color: #fac564;
    }
}
p{
    margin-top: 0;
    margin-bottom: 1rem;
    font-size: 15px;
    line-height: 1.8;
    font-weight: 300;
    color: gray;

    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-family: "Poppins", Arial, sans-serif;
}
`

export default Welcom
