import React, { useEffect } from 'react'
import styled from 'styled-components'
import { Link } from 'react-router-dom'
import data from '../data'
import Aos from 'aos'
import "aos/dist/aos.css"

const Melas = () => {
    useEffect(() => {
        Aos.init({
            duration: 3000
        })
    }, [])

    const newArray = data.filter(el => {
        return el.category == 'pizza'
    })

    return (
        <Section>
            <div className="container">
                <div  data-aos="fade-up" className="heading-section text-center">
                    <h2 className="mb-4">Hot Meals</h2>
                    <p className="mt-3 w-50 m-auto">Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts.</p>
                </div>
                <div className="row mt-5">
                    {newArray.slice(0, 4).map(el => {
                        return (
                            <div data-aos="flip-left"
                                data-aos-easing="ease-out-cubic"
                                data-aos-duration="2000" key={el.id} className="col-lg-3 col-md-3 col-sm-12 text-center">
                                <div className="menu-wrap">
                                    <div className="menu-img">
                                        <img src={el.img} className="img-lg-fluid" alt="" />
                                    </div>
                                    <div className="text">
                                        <h3><Link>{el.name}</Link></h3>
                                        <p className="desc">{el.desc}</p>
                                        <p className="price"><span>$ {el.price}</span></p>
                                        <p><Link className="btn btn-white btn-outline-white">Add to cart</Link></p>
                                    </div>
                                </div>
                            </div>
                        )
                    })}
                </div>
            </div>
        </Section>
    )
}
const Section = styled.div`
    padding: 5em 0;
    .heading-section h2{
        font-family: "Poppins", Arial, sans-serif;
        font-size: 30px;
        text-transform: uppercase;
        line-height: 1.8;
        font-weight: 300;
        color: #fff;
    }
    .heading-section{
        p{
            font-family: "Poppins", Arial, sans-serif;
            font-size: 15px;
            line-height: 1.8;
            font-weight: 300;
            color: gray;
            
        }
    }
    .menu-wrap .text h3 a{
        line-height: 1.4;
        color: #fff;
        font-weight: 400;
        font-family: "Josefin Sans", Arial, sans-serif;
        font-size: 1.75rem;
        text-decoration: none;
    }
    .menu-wrap .desc{
        font-family: "Poppins", Arial, sans-serif;
            font-size: 15px;
            line-height: 1.8;
            font-weight: 300;
            color: gray;
    }
    .menu-wrap .text .price {
    font-size: 16px;
    font-weight: 600;
    color: #fac564;
    }
    .menu-wrap .btn{
    border-radius: 0;
    margin-bottom: 0;
    color: #fac564;
    font-size: 14px;
    font-weight: 400;
    display: inline-block;
    border: 1px solid #fac564;
    background: none;
    margin-left: 5px;
    margin-right: 5px;
    padding: 8px 10px;
    :hover{
        color: #fff;
        background: #fac564;
    }
    }
`
export default Melas
