import React,{useEffect} from 'react'
import styled from 'styled-components'
import ImgBg from '../images/food.png'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPizzaSlice } from "@fortawesome/free-solid-svg-icons";
import Aos from 'aos'
import "aos/dist/aos.css"
const ServicesCom = () => {
    useEffect(() => {
        Aos.init({
            duration: 2000
        })
    }, [])
    return (
        <Main>
            <div data-aos="fade-up" className="container">
                <div className="text">
                    <h2 className="title">OUR SERVICES</h2>
                    <p className="text-center">Far far away, behind the word mountains, far from the countries Vokalia and
                        Consonantia, there live the blind texts.</p>
                </div>
                <div class="d-lg-flex">
                    <Cards data-aos="fade-right" class="col-md-4">
                        <div class="media d-block text-center block-6 services">
                            <div class="icon d-flex justify-content-center align-items-center mb-5">
                                <span class="flaticon-diet">
                                     <i class="fas fa-diagnoses"></i>
                                </span>
                            </div>
                            <div class="media-body">
                                <h3 class="heading">Healthy Foods</h3>
                                <p>Even the all-powerful Pointing has no control about the blind texts it is an almost unorthographic.</p>
                            </div>
                        </div>
                    </Cards>
                    <Cards data-aos="zoom-in" class="col-md-4">
                        <div class="media d-block text-center block-6 services">
                            <div class="icon d-flex justify-content-center align-items-center mb-5">
                                <span class="flaticon-bicycle"><i class="far fa-truck"></i></span>
                            </div>
                            <div class="media-body">
                                <h3 class="heading">Fastest Delivery</h3>
                                <p>Even the all-powerful Pointing has no control about the blind texts it is an almost unorthographic.</p>
                            </div>
                        </div>
                    </Cards>
                    <Cards data-aos="fade-left" class="col-md-4">
                        <div class="media d-block text-center block-6 services">
                            <div class="icon d-flex justify-content-center align-items-center mb-5"><span className="flaticon-pizza-1"><FontAwesomeIcon icon={faPizzaSlice}/></span></div>
                            <div class="media-body">
                                <h3 className="heading">Original Recipes</h3>
                                <p className="fw-lighter">Even the all-powerful Pointing has no control about the blind texts it is an almost unorthographic.</p>
                            </div>
                        </div>
                    </Cards>
                </div>
            </div>
        </Main>
    )
}
const Main = styled.div`
    padding: 3em 0;
    background: url(${ImgBg});
    background-repeat: repeat;
    background-position: center center;
    @media (min-width: 768px){
        .text {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 58.33333%;
    flex: 0 0 58.33333%;
    max-width: 58.33333%;
   }
}
    .text{
        text-align: center;
        margin: auto;
        width: 100%;
        p{
            font-size: 15px;
            font-family: "Poppins", Arial, sans-serif;
            color: #5c3d03;
            font-size: 15px;
            line-height: 1.8;
            font-weight: 300;
        }
    }
`
const Cards = styled.div`
    padding: 4em 0;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
.services h3 {
    font-size: 18px;
    margin-bottom: 20px;
    color: black;
    text-transform: uppercase;
    line-height: 1.4;
    font-family: "Josefin Sans", Arial, sans-serif;
}
p{
    font-family: "Poppins", Arial, sans-serif;
    font-size: 15px;
    line-height: 1.8;
    font-weight: 300;
    color: #5c3d03;
}
    @media (min-width: 768px){
        .col-md-4 {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 33.33333%;
    flex: 0 0 33.33333%;
    max-width: 33.33333%;
  }
}
.services .icon {
    position: relative;
    width: 100px;
    height: 100px;
    margin: 0 auto;
    z-index: 1;
 
}
.services .icon span {
    font-size: 60px;
    color: #5c3d03;
    z-index: -1;
}
span{
    
    width: 100px;
    height: 100px;
    margin: 0 auto;
    bordeR: 1px solid #bf7e06;
    transition: all 0.3s ease;
    :hover{
        transform: rotate(45deg);
        background: #fac564;
        border: 1px solid #bf7e06;
    }
}

`
export default ServicesCom
