import React,{useEffect} from 'react'
import styled from 'styled-components'
import Aos from 'aos'
import "aos/dist/aos.css"

const ContactCom = () => {
    useEffect(() => {
        Aos.init({
            duration: 3000
        })
    }, [])
    return (
        <Section>
            <div className="contact-in">
                <div  data-aos="zoom-in-down" className="contact-map">
                    <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d158857.72810784922!2d-0.24168043751141846!3d51.52877184053823!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x47d8a00baf21de75%3A0x52963a5addd52a99!2z0JvQvtC90LTQvtC9LCDQktC10LvQuNC60L7QsdGA0LjRgtCw0L3QuNGP!5e0!3m2!1sru!2s!4v1625669825061!5m2!1sru!2s" allowFullScreen="" loading="lazy"></iframe>
                </div>
                <div className="contact-form">
                    <h3>Contact Us</h3>
                    <form>
                        <input type="text" placeholder="Name" className="contact-form-txt" />
                        <input type="mail" placeholder="Email" className="contact-form-txt" />
                        <textarea placeholder="Message" className="contact-form-txtarea"></textarea>
                        <button type="button" class="contact-form-btn btn btn-outline-warning">Send</button>
                    </form>
                </div>
            </div>
        </Section>
    )
}
const Section = styled.div`
     padding-top: 5em;
     width: 100%;
     height: 100%;
     display: grid;
     align-items: center;
     /* background: #8e2de2; */
     .contact-in{
         width: 100%;
         height: auto;
         margin: auto;
         display: flex;
         flex-wrap: wrap;
         /* padding: 10px; */
         /* border-radius: 10px; */
         /* background: #000; */
         /* box-shadow: 0px 0px 10px 0px #666; */
     }
     .contact-map{
         width: 100%;
         height: auto;
         flex: 50%;
     }
     .contact-map iframe{
         width: 90%;
         height: 100%;
         @media (max-width: 500px){
             width: 100%;
             height: 250px;
             margin-bottom: 1em;
         }
     }
     .contact-form{
         width: 100%;
         height: auto;
         flex: 50%;
         /* text-align: center; */
         background: none;
     }
     .contact-form h3{
         margin-bottom: 10px;
         /* text-align: center !important; */
         color: white;
         text-align: start;
         font-family: "Josefin Sans", Arial, sans-serif;
     }
     .contact-form-txt{
         width: 90%;
         height: 40px;
         color: #fff;
         border-top: none;
         border-left: none;
         border-right: none;
         outline: none;
         margin-bottom: 20px;
         padding: 15px;
         background: none;
          :hover{
            border-bottom: 3px solid #fac564;
         }
     }
     .contact-form-txt ::placeholder{
         color: #aaa;

     }
     .contact-form-txtarea{
         width: 90%;
         resize: none;
         height: 130px;
         color: #fff    ;
         /* border: 1px solid #bcbcbc; */
         border-top: none;
         border-left: none;
         border-right: none;
         background: none;
         margin-bottom: 20px;
         padding: 15px;
         font-family: "Poppins", Arial, sans-serif;
     }
     .contact-form-txtarea ::placeholder{
         color: #aaa;
         
     }
     .contact-form-btn{
        border: none;
        color: #fff;
        padding: 8px 10px;
        cursor: pointer;
        font-size: 18px;
        margin: 20px;
        :hover{
            color: #000;
        }
     }
     @media screen and (max-width: 600px){
         .contact-in{
             display: block;
             width: 100%;
             margin: auto;
         }
     }
`

export default ContactCom
