import React, { useContext } from 'react'
import Img from '../images/bg_4.jpg'
import styled from 'styled-components'
import { Context } from '../context'
import Footer from './Footer'

const ShopCard = () => {
    const { cart } = useContext(Context)
    console.log(cart)
    return (
        <div>
            <Section>
                <div className="container">
                    <div className="align-items-center row d-flex">
                        {cart.map(el => {
                            console.log(el)
                            return (
                                <div key={el.idDrink} data-aos="flip-left"
                                    data-aos-easing="ease-out-cubic"
                                    data-aos-duration="2000" id="Card" className="col-lg-6 col-sm-12">
                                    <div className="card text-center">
                                        <div className="card-img m-auto">
                                            <img src={el.img} alt="Photo" className="img " />
                                        </div>
                                        <div className="card-heder">
                                            <h3 className="card-name">{el.name}</h3>
                                            <p className="card-text">{el.price}</p>
                                            <p className="price">$ {el.price}</p>
                                        </div>
                                    </div>
                                </div>
                            )
                        })}
                    </div>
                </div>
            </Section>
            <Footer />
        </div>
    )
}
const Section = styled.div`
    background: url(${Img}) no-repeat fixed;
    min-height: 60vh;
    object-fit: cover;
    .heading{
        text-align: center;
        font-size: 30px;
        font-weight: 400;
        color: black;
        line-height: 1.4;
        font-family: "Josefin Sans", Arial, sans-serif;
        color: #fff;
    }
    .btn-container{
        text-align: center;
        .pagination-btn{
           
            border-radius: 5px;
            border: 1px solid #fac564;
            color: #fac564;
            background: none;
            width: max-content;
            outline: none;
            /* border: none; */
            padding: 0.5rem;
            margin: 0.5rem; 
            margin-top: 3rem;
            &:hover{
                color: #fff;
                background: #fac564;
        }
    }
    }
    
p{
    display: block; 
    text-align: center;
    font-family: "Poppins", Arial, sans-serif;
    font-size: 15px;
    line-height: 1.8;
    font-weight: 300;
    margin: 0 0 16px;
    color: gray;
    margin-block-start: 1em;
    margin-block-end: 1em;
}
    #Card{
        padding: 2em 0 0 2em;
        @media (max-width: 500px){
            padding: 1em 2em;
        }
    }
    .card-heder h3{
        margin-bottom: 0.5rem;
    font-family: inherit;
    font-weight: 500;
    line-height: 1.2;
    font-size: 1.7rem;
    font-family: "Josefin Sans", Arial, sans-serif;
    color: #fac564;
    }
    .card-heder{
        padding: 1em;
        .card-text{
            font-family: "Poppins", Arial, sans-serif;
            font-size: 15px;
            line-height: 1.8;
            font-weight: 300;
            color: gray;
        }
        .price{
            color: #fac564;
        }
    }
    .card{
        margin: auto;
        background: none;
        width: 80%;
        height: 100%
    }
    .img{
        width: 200px;
        height: 200px;
        border-radius: 50%;
        object-fit: cover;
    } 
    form{
        input{
            background: none;
        }
    }
`
export default ShopCard
