import React,{useEffect} from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import Img1 from '../images/image_1.jpg'
import Img2 from '../images/image_2.jpg'
import Img3 from '../images/image_3.jpg'
import Aos from 'aos'
import "aos/dist/aos.css"

const BlogCom = () => {
    useEffect(() => {
        Aos.init({
            duration: 3000
        })
    }, [])
    return (
        <Section>
            <div className="col-md-12 heading-section text-center">
                <h2 className="heading-text mb-4">Recent from blog</h2>
                <p>Far far away, behind the word mountains, far from the countries Vokalia and<br /> Consonantia, there live the blind texts.</p>
            </div>
            <div className="container-xl">
                <div className="row row-cols-1 row-cols-md-3 g-4 text-center">
                    <div className="col">
                        <div data-aos="flip-left"
                            data-aos-easing="ease-out-cubic"
                            data-aos-duration="2000" className="card">
                            <img src={Img1} class="card-img-top img-lg-fluid" alt="..." />
                            <div className="text py-4 d-block">
                                <div className="meta">
                                    <div className="me-4"><Link>Sept 10, 2018</Link></div>
                                    <div className="me-4"><Link>Admin</Link></div>
                                    <div><Link className="meta-chat"><span className="icon-chat"><i className="far fa-envelope"></i></span> 3</Link></div>
                                </div>
                                <h3 className="heading-title mt-2"><Link>The Delicious Pizza</Link></h3>
                                <p>A small river named Duden flows by their place and supplies it with the necessary regelialia.</p>
                            </div>
                        </div>
                    </div>
                    <div data-aos="flip-left"
                        data-aos-easing="ease-out-cubic"
                        data-aos-duration="2000" className="col">
                        <div className="card">
                            <img src={Img2} className="card-img-top img-lg-fluid" alt="..." />
                            <div className="text py-4 d-block">
                                <div className="meta">
                                    <div className="me-4"><Link>Sept 10, 2018</Link></div>
                                    <div className="me-4"><Link>Admin</Link></div>
                                    <div><Link className="meta-chat"><span className="icon-chat"><i className="far fa-envelope"></i></span> 3</Link></div>
                                </div>
                                <h3 className="heading-title mt-2"><Link>The Delicious Pizza</Link></h3>
                                <p>A small river named Duden flows by their place and supplies it with the necessary regelialia.</p>
                            </div>
                        </div>
                    </div>
                    <div data-aos="flip-left"
                        data-aos-easing="ease-out-cubic"
                        data-aos-duration="2000" className="col">
                        <div className="card">
                            <img src={Img3} className="card-img-top" alt="..." />
                            <div className="text py-4 d-block">
                                <div className="meta">
                                    <div className="me-4"><Link>Sept 10, 2018</Link></div>
                                    <div className="me-4"><Link>Admin</Link></div>
                                    <div><Link className="meta-chat"><span className="icon-chat"><i className="far fa-envelope"></i></span> 3</Link></div>
                                </div>
                                <h3 className="heading-title mt-2"><Link>The Delicious Pizza</Link></h3>
                                <p>A small river named Duden flows by their place and supplies it with the necessary regelialia.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Section>
    )
}


const Section = styled.div`
padding: 7em 0;
     .heading-section{
        padding: 3em 0;
     }
    .heading-text{
    line-height: 1.4;
    color: #fff;
    font-weight: 400;
    font-family: "Josefin Sans", Arial, sans-serif;
    font-size: 30px;
    font-weight: 400;
    text-transform: uppercase;
    
    @media (min-width: 768px){
        .heading-section{
    -webkit-box-flex: 0;
    -ms-flex: 0 0 58.33333%;
    flex: 0 0 58.33333%;
    max-width: 58.33333%;
     }
  }
}
.heading-section p{
    font-family: "Poppins", Arial, sans-serif;
    font-size: 15px;
    line-height: 1.8;
    font-weight: 300;
    color: gray;
    @media (max-width: 500px){
        width: 90%;
        height: 145%;
        object-fit: cover;
        margin: auto;
    }
}
img {
    width: 100%;
    height: 270px;
    object-fit: cover;
    @media (max-width: 500px){
        width: 80%;
        height: 145%;
        object-fit: cover;
        margin: auto;
    }
}
.meta{
            display: flex;
            align-items: center;
            a{
                text-decoration: none;
                font-size: 15px;
                line-height: 1.8;
                font-weight: 300;
                color: gray;
            }
            @media (max-width: 500px){
                display: none;
            }
        }
.card{
    /* width: 100%; */
    border: none;
    background: none;
    p{
        color: gray;
    }
}
.heading-title{
        line-height: 1.4;
        font-size: 18px;
        font-weight: 400;
        font-family: "Josefin Sans", Arial, sans-serif;
        text-transform: inherit;
        a{
            color: #fff;
        }
    }
a{
    text-decoration: none;
}
`
export default BlogCom
