import React from 'react'
import styled from 'styled-components'
import { Link } from 'react-router-dom'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPizzaSlice,faShoppingCart } from "@fortawesome/free-solid-svg-icons";
import { Navbar, Nav, Container, NavbarBrand } from 'react-bootstrap';

const NavbarCom = () => {
    return (
        <Section>
            <Navbar collapseOnSelect expand="lg" variant="dark">
                <Container>
                    <NavbarBrand>
                        <Link to='/' className='navbar-brand'><span className="flaticon-pizza-1 mr-1">
                            <FontAwesomeIcon icon={faPizzaSlice} />
                        </span>Pizza<br />
                            <small>Delicous</small>
                        </Link>
                    </NavbarBrand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse className='navbar-collapse collapse' id="responsive-navbar-nav">
                        <Nav className="ms-auto">
                            <Link to='/' className="nav-link">Home</Link>
                            <Link to='/menu' className="nav-link">Menu</Link>
                            <Link to='/services' className="nav-link">Services</Link>
                            <Link to='/blog' className="nav-link">Blog</Link>
                            <Link to='/about' className="nav-link">About</Link>
                            <Link to='/contact' className="nav-link">Contact</Link>
                            <Link to="/shopcard" className="nav-link"><FontAwesomeIcon icon={faShoppingCart} /><sup
                            className="sup"></sup></Link>
                            <Link to='/admin' className="nav-link">Admin</Link>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </Section>
    )
}
const Section = styled.div`
    width: 100%;
    height: auto;
    background: var(--main-bg);
    .navbar-brand small {
    text-transform: uppercase;
    font-size: 11px;
    display: block;
    text-align: center;
    color: #fac564;
    letter-spacing: 4px;
}
.navbar-brand {
    display: inline-block;
    padding-top: 0.3125rem;
    padding-bottom: 0.3125rem;
    margin-right: 1rem;
    font-size: 1.30rem;
    font-family: "Josefin Sans", Arial;
    line-height: inherit;
    white-space: nowrap;
    span{
        color: #fac564;
    }
}
a {
    color: #78d5ef;
    text-decoration: none;
    background-color: transparent;
    -webkit-text-decoration-skip: objects;
}
.navbar-collapse {
    -ms-flex-preferred-size: 100%;
    flex-basis: 100%;
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;

.nav-link {
    font-size: 18px;
    padding-top: .9rem;
    padding-bottom: .9rem;
    padding-left: 0 5rem;
    padding: 0 0 0 3rem;
    color: rgba(255, 255, 255, 0.9);
    font-weight: 400;
    opacity: 1 !important;
    font-family: "Josefin Sans", Arial;
    :hover{
        color: #fac564;
    }
}
}
`
export default NavbarCom
