import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { Link } from 'react-router-dom'
import data from '../data'
import utils from '../pages/utils'
import Aos from 'aos'
import "aos/dist/aos.css"

const CardPagination = () => {
    const [array, setArray] = useState([])
    console.log(array);
    const [page, setPage] = useState(1)
    const [createButton, setCreateButton] = useState([])

    useEffect(() => {
        Aos.init({
            duration: 2000
        })
        setArray(DisplayList(data, 6, page))   
    }, [page])

    const nextPage = (index) => {
        setPage(index + 1)
        setArray(DisplayList(array, 6, 0))
        console.log(page)
    }

    // pagination
    function DisplayList(array, rows, current_page) {
        current_page--
        let start = current_page * rows;
        let end = start + rows;
        let pagination = array.slice(start, end);
        setCreateButton(pagination)
        return pagination
    }

    return (
        <Section className="ftco-section">
            <div className="container">
                <div className="row justify-content-center mb-5 pb-3">
                    <div className="col-md-7 heading-section text-center">
                        <h2 className="mb-4">Read our blog</h2>
                        <p className="text">Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts.</p>
                    </div>
                </div>
                <div className="row d-flex">
                    {array.map(el => {
                        return (
                            <div data-aos="flip-left" key={el.id} className="cards col-lg-4 col-md-4 col-sm-12 d-flex">
                                <div className="text-center">
                                    <Link className="block-20">
                                        <img src={el.img} className="img" alt="" />
                                    </Link>
                                    <div className="text py-4 d-block">
                                        <div className="meta">
                                            <div><Link>Sept 10, 2018</Link></div>
                                            <div><Link>Admin</Link></div>
                                            <div><Link class="meta-chat"><span className="icon-chat"><i className="far fa-envelope"></i></span> 3</Link></div>
                                        </div>
                                        <h3 className="heading mt-2">The Delicious Pizza</h3>
                                        <p className="card-text">A small river named Duden flows by their place and supplies it with the necessary regelialia.</p>
                                        <button className="button">Add to card</button>
                                    </div>
                                </div>
                            </div>
                        )
                    })}    
                     <div className="btn-container">
                        {createButton.map((el, index) => {
                            return <button key={el.idDrink} className='pagination-btn' onClick={() => nextPage(index)}>{index + 1}</button>
                        })}
                    </div> 
                </div>
            </div>
        </Section>
    )
}
const Section = styled.div`
    padding: 5em 0;
    /* .cards{
        width: 100%;
        color: #fff;
        margin: 0 auto;
    } */
    .btn-container{
        text-align: center;
        .pagination-btn{
           
            border-radius: 5px;
            border: 1px solid #fac564;
            color: #fac564;
            background: none;
            width: max-content;
            outline: none;
            /* border: none; */
            padding: 0.5rem;
            margin: 0.5rem; 
            margin-top: 3rem;
            &:hover{
                color: #fff;
                background: #fac564;
        }
    }
}           
    .heading-section{
        h2{
            font-size: 30px;
            margin-bottom: 20px;
            color: black;
            text-transform: uppercase;
            color: #fff;
            font-weight: 300;
            line-height: 1.4;
            font-family: "Josefin Sans", Arial, sans-serif;
        }
        .text{
            font-family: "Poppins", Arial, sans-serif;
            font-size: 15px;
            line-height: 1.8;
            font-weight: 300;
            color: gray;

        }
    }
    .heading{
        margin-bottom: 0.5rem;
    font-family: inherit;
    font-weight: 500;
    line-height: 1.2;
    font-size: 1.7rem;
    font-family: "Josefin Sans", Arial, sans-serif;
    color: #fac564;
    }
    .cards{
        .card-text{
            font-family: "Poppins", Arial, sans-serif;
            font-size: 15px;
            line-height: 1.8;
            font-weight: 300;
            color: gray;
        }
        .meta{
            display: flex;
            align-items: center;
            justify-content: space-evenly;
            a{
                text-decoration: none;
                font-size: 15px;
                line-height: 1.8;
                font-weight: 300;
                color: gray;
            }
        }
    }
    .card{
        background: none;
        width: 80%;
        height: 100%
    }
    .img{
        width: 170px;
        height: 170px;
        border-radius: 50%;
    }
    .button{
    border-radius: 0;
    margin-bottom: 0;
    color: #fac564;
    font-size: 18px;
    font-weight: 400;
    display: inline-block;
    border: 1px solid #fac564;
    background: none;
    margin-left: 5px;
    margin-right: 5px;
    padding: 8px 10px;
    &:hover{
        color: white;
        background: #fac564;
    }
}

`
export default CardPagination
