import React, { useState,useContext } from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import data from '../data'
import {Context} from '../context'

const MenuCom = () => {
    const {addtoCart} = useContext(Context)

    const newArray = data.filter(el => {
        return el.category == 'pizza'
    })

    return (
        <Main>
            <div className="container">
                <div className="text">
                    <h2 className="heading px-5">HOT PIZZA MEALS</h2>
                    <p className="text mb-5">Far far away, behind the word mountains, far from
                        the countries Vokalia and <br />Consonantia, there live the blind texts.</p>
                </div>
            </div>
            {/* <div className="container-fluid"> */}
            <Menu className="container">
                <div className="align-items-center row">
                    {newArray.map(el => {
                        return (
                            <div key={el.id} id="cards" className="col-lg-6  col-sm-12 d-flex">
                                <div className="services-wrap text-center d-lg-flex">
                                    <Link className='img'>
                                        <IMG src={el.img} alt="" />
                                    </Link>
                                    <div className="text-heading p-4">
                                        <h3>{el.name}</h3>
                                        <p>{el.desc}</p>
                                        <p className="price"><span>${el.price}</span><button onClick={() =>  addtoCart(el)} className="ml-2 btn btn-outline-white">Order</button></p>
                                    </div>
                                </div>
                            </div>
                        )
                    })}
                </div>
            </Menu>
            {/* </div> */}
        </Main>
    )
}
const IMG = styled.img`
width: 236px;
height: 236px;
`

const Div = styled.div`
width: 100%;
.imges{
    border-radius: 50px;
    width: 150px;
    height: 150px;
}
`
const Main = styled.div`
    padding: 7em 0;
    .text{
        padding-bottom: 1rem !important;
    }
.heading{
    text-align: center;
    font-size: 30px;
    font-weight: 400;
    color: black;
    line-height: 1.4;
    font-family: "Josefin Sans", Arial, sans-serif;
    color: #fff;
}
p{
    display: block; 
    text-align: center;
    font-family: "Poppins", Arial, sans-serif;
    font-size: 15px;
    line-height: 1.8;
    font-weight: 300;
    margin: 0 0 16px;
    color: gray;
    margin-block-start: 1em;
    margin-block-end: 1em;
}
`
const Menu = styled.div`

#cards{
    padding: 2em;
    margin: auto;
    @media (max-width: 950px){
        margin: auto;
       padding:0;
    }
}
.services-wrap{
    /* height: 18rem; */
    width: 100% auto;

    background: rgba(0, 0, 0, 0.2) !important;
}
    h3{
        text-align: start;
        line-height: 1.4;
        color: #fff;
        font-weight: 400;
        font-family: "Josefin Sans", Arial, sans-serif;
    }
    p{
    text-align: start;
    font-family: "Poppins", Arial, sans-serif;
    font-size: 15px;
    line-height: 1.8;
    font-weight: 300;
    color: gray;
}
.services-wrap .img, .text {
    width: 50%;
}

.btn{
    font-weight: 400;
    text-align: center;
    border: 1px solid white;
    color: white;
    font-size: 1rem;
    line-height: 1.5;
    margin: 0 0 0 14px;
    :hover{
        background: #fac564;
        color: black;
    }
}
a{
    text-decoration: none;
}
.text-heading{
    background: rgba(0, 0, 0, 0.2);
    width: 50%;
    @media (max-width: 500px){
        width: 50%;
    }
    @media (max-width: 970px){
        width: 100%;
    }
}
`
export default MenuCom



