import React, { useState, useEffect,useContext } from 'react'
import styled from 'styled-components'
import Image from '../images/about.jpg'
import data from '../data'
import {Context} from '../context'
import Aos from 'aos'
import "aos/dist/aos.css"
const CategoryCom = () => {
  const {addtoCart} = useContext(Context)

    useEffect(() => {
        Aos.init({
            duration: 2000
        })
    }, [])


    const pizza = data.filter(el => {
        return el.category == 'pizza'
    })

    const [filter, setFilter] = useState(pizza)

    const filterByCategoryHandler = (cat) => {
        const category = data.filter(el => {
            return el.category == cat
        })
        setFilter(category)
    }

    return (
        <Section>
            <div className="Menu">
                <div className="container-fluid">
                    <div className="row d-md-flex">
                        <div className="col-lg-4 col-md-4 col-sm-12" id="image">

                        </div>
                        <div className="col-lg-8 col-md-8 col-sm-12">
                            <div className="buttons">
                                <button className="button" onClick={() => filterByCategoryHandler('pizza')}>Pizza</button>
                                <button className="button" onClick={() => filterByCategoryHandler('drinks')}>Drinks</button>
                                <button className="button" onClick={() => filterByCategoryHandler('burger')}>Burget</button>
                            </div>
                            <div className="cards">
                                <div className="row container-wrap">
                                    {filter.slice(0, 3).map(el => {
                                        return (
                                            <div data-aos="flip-left"
                                                data-aos-easing="ease-out-cubic"
                                                data-aos-duration="2000" key={el.id} className="col-lg-4 col-md-4 col-sm-12">
                                                <div className="card text-center">
                                                    <div className="card-img m-auto">
                                                        <img key={el.img} src={el.img} alt="Photo" className="img" />
                                                    </div>
                                                    <div className="card-heder">
                                                        <h3 className="card-name">{el.name}</h3>
                                                        <p className="card-text">{el.desc}</p>
                                                        <p className="price">${el.price}</p>
                                                    </div>
                                                    <button onClick={() =>  addtoCart(el)} className="btn btn-outline-warning">Add to card</button>
                                                </div>
                                            </div>
                                        )
                                    })}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Section>
    )
}
const Section = styled.div`
    #image{
        height: 500px;
        background-image: url(${Image});
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
    }
    .cards{
        width: 100%;
        color: #fff;
        margin: 0 auto;
        @media (max-width: 500px) and (min-width: 360px){
            max-width: 500px;
            margin: auto;
        }
    }
    .cards h3{
        margin-bottom: 0.5rem;
    font-family: inherit;
    font-weight: 500;
    line-height: 1.2;
    font-size: 1.7rem;
    font-family: "Josefin Sans", Arial, sans-serif;
    color: inherit;
    }
    .cards{
        .card-text{
            font-family: "Poppins", Arial, sans-serif;
            font-size: 15px;
            line-height: 1.8;
            font-weight: 300;
            color: gray;
        }
    }
    .buttons{
        padding: 48px;
    }
    .card{
        background: none;
        width: 100%;
        height: 100%
    }
    .img{
        width: 170px;
        height: 170px;
        border-radius: 50%;
    }
    .button{
    border-radius: 0;
    margin-bottom: 0;
    color: #fac564;
    font-size: 18px;
    font-weight: 400;
    display: inline-block;
    border: 1px solid #fac564;
    background: none;
    margin-left: 5px;
    margin-right: 5px;
    padding: 8px 10px;
    :hover{
        color: #fff;
        background: #fac564;
    }
    @media (max-width: 500px){
            display: block;
            width: 100%;
            margin: auto;
            margin-top: 10px;
        }
}
`
export default CategoryCom
