import React, { useEffect } from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import CountUp from 'react-countup'
import Img from '../images/gallery-1.jpg'
import Img1 from '../images/gallery-2.jpg'
import Img2 from '../images/gallery-3.jpg'
import Img3 from '../images/gallery-4.jpg'
import Bg from '../images/bg_2.jpg'
import data from '../data'
import Aos from 'aos'
import "aos/dist/aos.css"

const HeppiCustomer = () => {
    useEffect(() => {
        Aos.init({
            duration: 3000
        })
    }, [])
    return (
        <Main>
            <section className="ftco-gallery">
                <div className="container-fluid">
                    <div className="row">
                        <div data-aos="flip-left"
                            data-aos-easing="ease-out-cubic"
                            data-aos-duration="2000" className="col-lg-3 col-md-3 img img-lg-fluid">
                            {/* img */}
                        </div>
                        <div data-aos="flip-left"
                            data-aos-easing="ease-out-cubic"
                            data-aos-duration="2000" className="col-lg-3 col-md-3 img1 img-lg-fluid">
                            {/* img1 */}
                        </div>
                        <div data-aos="flip-left"
                            data-aos-easing="ease-out-cubic"
                            data-aos-duration="2000" className="col-lg-3 col-md-3 img2 img-lg-fluid">
                            {/* img2 */}
                        </div>
                        <div data-aos="flip-left"
                            data-aos-easing="ease-out-cubic"
                            data-aos-duration="2000" className="col-lg-3 col-md-3 img3 img-lg-fluid">
                            {/* img3 */}
                        </div>
                    </div>
                </div>
            </section>
            <section className="ftco-counter bg-dark img" id="section-counter">
                <div className="overlay"></div>
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-md-10">
                            <div className="row">
                                <div data-aos="fade-right" className="col-lg-3 col-md-6 col-sm-6 d-flex justify-content-center align-items-center counter-wrap">
                                    <div className="block-18 text-center">
                                        <div className="text">
                                            <div className="icon"><span className="flaticon-pizza-1"><i class="fal fa-pizza-slice"></i></span></div>
                                            <strong className="number" data-from="100" data-to="300" data-refresh-interval="10"><CountUp end={150} duration={10} />0</strong>
                                            <span className="text-span d-block">Pizza Branches</span>
                                        </div>
                                    </div>
                                </div>
                                <div data-aos="flip-left"
                                    data-aos-easing="ease-out-cubic"
                                    data-aos-duration="2000" className="col-lg-3 col-md-6 col-sm-6  d-flex justify-content-center align-items-center counter-wrap">
                                    <div className="block-18 text-center">
                                        <div className="text">
                                            <div className="icon"><span className="flaticon-medal"><i class="fal fa-medal"></i></span></div>
                                            <strong className="number" data-from="100" data-to="600" data-refresh-interval="10"><CountUp end={150} duration={10} />0</strong>
                                            <span className="text-span d-block">Number of Awards</span>
                                        </div>
                                    </div>
                                </div>
                                <div data-aos="flip-left"
                                    data-aos-easing="ease-out-cubic"
                                    data-aos-duration="2000" className="col-lg-3 col-md-6 col-sm-6 d-flex justify-content-center align-items-center counter-wrap">
                                    <div className="block-18 text-center">
                                        <div className="text">
                                            <div className="icon"><span className="flaticon-laugh"><i class="far fa-grin-wink"></i></span></div>
                                            <strong className="number" data-from="100" data-to="400" data-refresh-interval="10"><CountUp end={250} duration={15} />0</strong>
                                            <span className="text-span d-block">Happy Customer</span>
                                        </div>
                                    </div>
                                </div>
                                <div data-aos="fade-left" className="col-md-6 col-lg-3 d-flex justify-content-center align-items-center counter-wrap">
                                    <div className="block-18 text-center">
                                        <div className="text">
                                            <div className="icon"><span className="flaticon-chef"><i class="far fa-hat-chef"></i></span></div>
                                            <strong className="number" data-from="10" data-to="100" data-refresh-interval="10"><CountUp end={200} duration={8} />0</strong>
                                            <span className="text-span d-block">Staff</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </Main>
    )
}
const Main = styled.div`
.row{
    height: 300px;
    @media (max-width: 500px){
        height: 700px;
    }
}
.img{
    background-image: url(${Img});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}
.img1{
    background-image: url(${Img1});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}
.img2{
    background-image: url(${Img2});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}
.img3{
    background-image: url(${Img3});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}
.ftco-counter{
    background: url(${Bg}) fixed;
    /* background-position: 50% -187.25px; */
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}
#section-counter {
    position: relative;
    z-index: 0;
}
.overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #000;
    opacity: .8;
    z-index: -1;
}
.text{
    .icon{
        font-size: 30px;
        color: #fac564;
        width: 100%;
        height: 100%;
        padding: 8px 10px;
    }
    .text-span{
        color: #fac564;
        font-size: 15px;
        font-family: "Poppins", Arial, sans-serif;  
        padding: 20px 0;
    }
}
.number{
    color: #fac564;
    font-size: 30px;
    font-family: "Poppins", Arial, sans-serif;  
    font-weight: 100;
}
`
export default HeppiCustomer