import React,{useEffect} from 'react'
import styled from 'styled-components'
import { Link } from 'react-router-dom'
import Aos from 'aos'
import "aos/dist/aos.css"
const OpenDay = () => {
    useEffect(() => {
        Aos.init({
            duration: 3000
        })
    }, [])
    return (
        <Main>
            <Section className="section-main">
                <div className="container info">
                    <div className="row  no-gutters">
                        <div className="col-md-4 d-flex ftco-animate fadeInUp ftco-animated">
                            <div className="icon"><span class="icon-phone"><i className="fas fa-phone-alt"></i></span></div>
                            <div className="text">
                                <h3>000 (123) 456 7890</h3>
                                <p>A small river named Duden flows</p>
                            </div>
                        </div>
                        <div className="col-md-4 d-flex ftco-animate fadeInUp ftco-animated">
                            <div className="icon"><span className="icon-my_location"><i className="fas fa-location"></i></span></div>
                            <div className="text">
                                <h3>198 West 21th Street</h3>
                                <p>Suite 721 New York NY 10016</p>
                            </div>
                        </div>
                        <div className="col-md-4 d-flex ftco-animate fadeInUp ftco-animated">
                            <div className="icon"><span class="icon-clock-o"><i className="far fa-clock"></i></span></div>
                            <div className="text">
                                <h3>Open Monday-Friday</h3>
                                <p>8:00am - 9:00pm</p>
                            </div>
                        </div>
                    </div>
                </div>
            </Section>
            <SectionSocial className="social col-lg-6 d-md-flex pl-md-5 p-4 align-items-center">
                <ul data-aos="fade-up" className="social-icon">
                    <li><Link><span className="icon-twitter"><i className="fab fa-twitter"></i></span></Link></li>
                    <li><Link><span className="icon-facebook"><i className="fab fa-facebook-f"></i></span></Link></li>
                    <li><Link><span className="icon-instagram"><i className="fab fa-instagram"></i></span></Link></li>
                </ul>
            </SectionSocial>

        </Main>
    )
}
const Main = styled.main`
    display: flex;
    @media only screen and (max-width: 600px){
        flex-direction: column;
        .section-main{
            width: 100%;
        }
        .social{
            width: 100%;
        }
    }
`

const Section = styled.div`
    width: 70%;
    background: #000;
    padding: 40px;
    @media (min-width: 1200px){
        .ftco-intro .wrap .info {
    width: calc(100% - 35%);
    padding: 2% 5% 2% 12%;
}
}
  .text {
    width: calc(100% ,40px);
}
h3{
    line-height: 1.4;
    color: #fff;
    font-weight: 400;
    font-family: "Josefin Sans", Arial, sans-serif;
}
h3{
    font-size: 16px;
    font-family: 'Josefin Sans', sans-serif;
}
p {
    display: block;
    color: grey;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-family: "Poppins", Arial, sans-serif;
}
.icon {
    width: 40px;
    color: #fac564;
}
`
const SectionSocial = styled.div`
    background: #fac564;
    width: 30%;
    ul{
      display: flex;
    }
    .social-icon li {
    list-style: none;
    line-height: 0;
    padding: 0 10px;
}
a{
    color: white;
}
`

export default OpenDay
