import React from 'react'
import styled from 'styled-components';
import OpenDay from '../components/OpenDay';
import Welcom from '../components/Welcom';
import ServicesCom from '../components/ServicesCom';
import MenuCom from '../components/MenuCom';
import Pricing from '../components/Pricing';
import BlogCom from '../components/BlogCom';
import HeppiCustomer from '../components/HeppiCustomer';
import Footer from '../components/Footer'
import ContactCom from '../components/ContactCom';
import CategoryCom from '../components/CategoryCom';
import ShopCard from '../components/ShopCard';
import "../../src/home.css"
import { Link } from 'react-router-dom';
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
import Img1 from '../images/bg_2.png'
import Img2 from '../images/bg_1.png'
import Img3 from '../images/bg_4.jpg'
// Import Swiper styles
import "swiper/swiper.min.css";
import "swiper/components/pagination/pagination.min.css"


// import Swiper core and required modules
import SwiperCore, {
    Autoplay,Pagination,Navigation
  } from 'swiper/core';
  
  // install Swiper modules
  SwiperCore.use([Autoplay,Pagination,Navigation]);


const Home = () => {
    return (
        <Main>
            <Section className='slayder'>
                <Swiper spaceBetween={10} centeredSlides={true} autoplay={{
                    "delay": 9000,
                    "disableOnInteraction": false
                }} pagination={{
                    "clickable": true
                }} navigation={true} className="mySwiper">
                    <SwiperSlide>
                        <div className="slider-item2">
                            <div className="overlay"></div>
                            <div data-aos="fade-right" className="container">
                                <div className="row slider-text justify-content-center align-items-center" data-scrollax-parent="true">
                                    <div className="col-lg-6 col-md-6 col-sm-12 text-lg-end text-sm-center">
                                        <span className="subheading text-warning">Delicions</span>
                                        <h1 className="mb-4">Italian Cuizine</h1>
                                        <p className="mb-4 mb-md-5 text-light">A small river named Duden flows by their place and supplies it with the necessary regelialia.</p>
                                        <Link to="/" className="btn btn-warning text-dark p-3 px-xl-4 py-xl-3">Order Now</Link>
                                        <Link className="btn btn-outline-warning border text-white button ms-2 p-3 px-xl-4 py-xl-3">View Menu</Link>
                                    </div>

                                    {/* <div className="col-lg-6 col-md-7 col-sm-12 text-center"> */}
                                    <img className="col-lg-6 col-md-6 col-sm-12 text-center d-lg-block d-none" src={Img2} />
                                    {/* </div> */}
                                </div>
                            </div>
                        </div>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="slider-item2">
                            <div className="overlay"></div>
                            <div className="container">
                                <div data-aos="fade-left" className="row slider-text justify-content-center align-items-center" data-scrollax-parent="true">
                                    {/* <div className="col-lg-6 col-md-7 col-sm-12 text-center"> */}
                                    <img className="col-lg-6 col-md-7 col-sm-12 text-center d-lg-block d-none" src={Img1} />
                                    {/* </div> */}
                                    <div className="col-lg-6 col-md-7 col-sm-12 text-lg-start">
                                        <span className="subheading text-warning">Delicions</span>
                                        <h1 className="mb-4">Italian Pizza</h1>
                                        <p className="mb-4 mb-md-5 text-light">A small river named Duden flows by their place and supplies it with the necessary regelialia.</p>
                                        <Link className="btn btn-warning p-3 px-xl-4 py-xl-3">Order Now</Link>
                                        <Link className="btn btn-outline-warning border-warning button text-white ms-2 p-3 px-xl-4 py-xl-3">View Menu</Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="slider-item text-center">
                            <div className="overlay"></div>
                            <div className="container">
                                <div data-aos="zoom-in" className="row slider-text justify-content-center align-items-center" data-scrollax-parent="true">

                                    <div className="col-md-7 col-sm-12 ftco-animate fadeInUp ftco-animated">
                                        <span className="subheading">Welcome</span>
                                        <h1 className="mb-4">We cooked your desired Pizza Recipe</h1>
                                        <p className="mb-4 mb-md-5">A small river named Duden flows by their place and supplies it with the necessary regelialia.</p>
                                        <Link className="btn btn-warning p-3 px-xl-4 py-xl-3">Order Now</Link>
                                        <Link className="btn btn-white btn-outline-warning ms-2 p-3 px-xl-4 py-xl-3">View Menu</Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </SwiperSlide>
                </Swiper>
            </Section>
            <OpenDay />
            <Welcom />
            <ServicesCom />
            <MenuCom />
            <Pricing />
            <HeppiCustomer />
            <CategoryCom />
            <BlogCom />
            <ContactCom />
            <Footer />
        </Main>
    )

}
const Main = styled.main`
   background: url(${Img3}) fixed ;
`
const Section = styled.div`
.row{
    width: 100%;
    height: 80vh;
    /* margin: 0 5rem; */
}
.swiper-container {
  width: 100%;
  height: 100%;
}
.img-fluid{
    height: max-content !important;
    width: max-content;
    object-fit: cover;
}
.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  span{
    color: #FAC564;
    font-family: 'Dancing Script', cursive;
    font-size: 30px;
  }
  h1{
    font-family: 'Hind', sans-serif;
    font-size: 4rem;
    color: white;
  }
  p{
     font: 20px;
     color: white;
     font-family: 'Quicksand', sans-serif;
  }
  .btn{
      color: white;
      :hover{
          color: yellow;
      }
    
  }
  @media (max-width: 500px){
      span{
          font-size: 15px;
          margin: auto;
      }
      h1{
          font-size: 2rem;
          margin: auto;
      }
      p{
        font-size: 15px;
          text-align: center;
      }
  }
}
.swiper-slide img {
  display: block;
}
`
export default Home

